import request from "../utils/request";

const api = {
  login: "/api/login",
  userInfo: "/my/getInfo",
};

export function LoginApi(paramster) {
  return request({
    url: api.login,
    method: "post",
    data: paramster,
  });
}

export function getUeseApi(paramster) {
  return request({
    url: api.userInfo,
    method: "get",
    data: paramster,
  });
}
