import React from "react";
import { InfoCircleOutlined } from "@ant-design/icons";
import { Button, Checkbox, Form, Input, Switch } from "antd";
// 封装表格组件
export default function MenuForm(props) {
  const onFinish = (values) => {
    console.log("Success:", values);
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  return (
    <div>
      {" "}
      <Form
        name="basic"
        labelCol={{
          span: 4,
        }}
        wrapperCol={{
          span: 16,
        }}
        initialValues={{
          remember: true,
        }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
        labelWrap={true}
      >
        <Form.Item label="菜单类型" name="username">
          <Button>一级菜单</Button>
          <Button>子菜单</Button>
          <Button>按钮/权限</Button>
        </Form.Item>

        <Form.Item label="菜单名称" name="password">
          <Input />
        </Form.Item>
        <Form.Item label="访问路径" name="password">
          <Input />
        </Form.Item>
        <Form.Item label="前端组件" name="password">
          <Input />
        </Form.Item>
        <Form.Item
          label="组件名称"
          name="password"
          tooltip={{
            title: "Tooltip with customize icon",
            icon: <InfoCircleOutlined />,
          }}
        >
          <Input />
        </Form.Item>
        <Form.Item label="默认跳转地址" name="password">
          <Input />
        </Form.Item>
        <Form.Item label="排序" name="password">
          <Input />
        </Form.Item>

        <Form.Item label="是否显示" valuePropName="checked">
          <Switch />
        </Form.Item>
        <Form.Item
          wrapperCol={{
            offset: 8,
            span: 16,
          }}
        >
          <Button type="primary" htmlType="submit">
            提交
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
